@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
:root {
  --font-family: 'Manrope', sans-serif;

  --gradient-text: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
  --gradient-bar: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);
  
  --color-bg: #040C18;
  --color-footer : #031B34;
  --color-blog: #042c54;
  --color-text: #81AFDD;
  --color-subtext: #FF8A71;
}
.gpt3__blog {
    display: flex;
    flex-direction: column;
}
.gpt3__blog-heading {
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;

}
.gpt3__blog-heading h1 {
    font-family: var(--font-family);
    font-size: 62px;
    font-weight: 800;
    line-height: 75px; /* 120.968% */
    letter-spacing: -0.04px;
}
.gpt3__blog-container {
    display: flex;
    flex-direction: row;
}
.gpt3__blog-container_groupA {
    display: flex;
    flex:0.75 1;
    margin-right: 2rem;

} 
.gpt3__blog-container_groupB {
    display: grid;
    flex:1 1;
   grid-template-columns: repeat(2, 1fr);
   grid-gap: 2rem;
    
} 
@media screen and (max-width:990px) {
    .gpt3__blog-container {
        flex-direction: column-reverse;
    }
    .gpt3__blog-container_groupA {
        margin: 2rem 0 ;
    }
    .gpt3__blog-container_groupA .gpt3__blog-container_article {
        width: 48%;
    }
    .gpt3__blog-container_groupA .gpt3__blog-container_article-image {
        height: 250px;
    }
}
@media screen and (max-width:700px) {
    .gpt3__blog-container_groupB {
        grid-template-columns: repeat(1, 1fr);
    }
    .gpt3__blog-container_groupA .gpt3__blog-container_article {
        width: 100%;
    }
    .gpt3__blog-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}
@media screen and (max-width:550px) {
    .gpt3__blog-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }
}    
.gpt3__blog-container_article {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background: var(--color-footer);
}
.gpt3__blog-container_article-image{
    width: 100%;
    height: 100%;
    background: var(--color-footer);
}
.gpt3__blog-container_article-image img {
    width: 100%;
    height: 100%;
}
.gpt3__blog-container_article-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    height: 100%;
}
.gpt3__blog-container_article-content p {
    color: #FFF;
    font-family: var(--font-family);
    font-size: 11.649px;
    font-weight: 700;
    line-height: 34.947px;
}
.gpt3__blog-container_article-content h3 {
    color: #FFF;
    font-family: var(--font-family);
    font-size: 25.11px;
    font-style: normal;
    font-weight: 800;
    line-height: 30.287px;
    margin-bottom: 5rem;
    cursor: pointer;
}
.gpt3__blog-container_article-content p:last-child {
    color: #FFF;
    font-family: var(--font-family);
    font-size: 11.649px;
    font-weight: 700;
    line-height: 34.947px;
    cursor: pointer;
}
@media screen and (max-width:550px) {
    .gpt3__blog-container_article-content h3 {
        font-size: 18px;
        line-height: 25px;
    }
}
.gpt3__brand {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.gpt3__brand div {
    flex: 1 1;
    max-width: 150px;
    min-width: 120px;
    margin: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
}
.gpt3__cta {
    display: flex;justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}
.gpt3__cta-content p {
    color: #0E0E0E;
    font-family:var(--font-family);
    font-size: 12px;
    font-weight: 500;
    line-height: 30px;
}
.gpt3__cta-content h3 {
    color: #000;
    font-family:var(--font-family);
    font-size: 24px;
    font-weight: 800;
    line-height: 45px;
}
.gpt3__cta-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2rem;
}
.gpt3__cta-btn button {
    border-radius: 40px;
    background: #000;
    color: #FFF;
    font-family:var(--font-family);
    font-size: 18px;
    font-weight: bold;
    line-height: 30px;
    padding: 0.5rem 1rem;
    border: none;
    outline:none;
    cursor: pointer;
    min-width: 150px;
}
@media screen and (max-width:650px) {
    .gpt3__cta {
        flex-direction: column;
    }
    .gpt3__cta-btn {
        margin: 2rem 0 0;
    }
}
@media screen and (max-width:550px) {
    .gpt3__cta {
       margin: 4rem 2rem;
    }
    .gpt3__cta-content h3 {
        font-size: 18px;
        line-height: 32px;
    }
    .gpt3__cta-btn button {
        font-size: 14px;
        line-height: 28px;
    }
}
.gpt3__features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    margin: 1rem;
}
.gpt3__features-container__feature-title {
    flex: 1 1;
    max-width: 180px;
    margin-right: 2rem;
}
.gpt3__features-container__feature-title h1 {
    color: #FFF;
    font-family: var(--font-family);
    font-size: 18px;
    font-weight: 800;
    line-height: 24px; /* 312.5% */
    letter-spacing: -0.96px;
}
.gpt3__features-container__feature-title div {
    width: 38px;
    background: var(--gradient-bar);
    height: 3px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}


.gpt3__features-container_feature-text {
    flex: 2 1;
    display: flex;
    max-width: 390px;
    
}
.gpt3__features-container_feature-text p {
    color: var(--color-text);
    font-family: var(--font-family);
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 24px; /* 187.5% */
}
@media screen and (max-width:550px) {
    .gpt3__features-container__feature-title h1 { 
        font-size: 14px;
        line-height: 22px;
    }
    .gpt3__features-container_feature-text p {
        font-size: 12px;
        line-height: 20px; 
    }
    .gpt3__features-container__feature {
        margin: 1rem 0;
        flex-direction: column;
    }
}
.gpt3__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 6rem;
}

.gpt3__navbar-links {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.gpt3__navbar-links_logo {
    margin-right: 2rem;
}
.gpt3__navbar-links_logo img {
    width: 62px;
    height: 16px;
}
.gpt3__navbar-links_container {
    display: flex;
    flex-direction: row;
}
.gpt3__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.gpt3__navbar-links_container p,
.gpt3__navbar-sign p, 
.gpt3__navbar-menu_container p{
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}
.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button {
    width: 152px;
    height: 58px;
    background: #FF4820;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border: 0;
    outline: none;
    cursor: pointer;
}
.gpt3__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}
.gpt3__navbar-menu svg {
    cursor: pointer;
}
.gpt3__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}
.gpt3__navbar-menu_container p {
    margin: 1rem 0;
}
.gpt3__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width:1050px) {
    .gpt3__navbar-links_container {
        display: none;
    }
    .gpt3__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width:700px) {
    .gpt3__navbar {
        padding: 2rem 4rem;
    }
}
@media screen and (max-width:500px) {
    .gpt3__navbar {
        padding: 2rem;
    }
    .gpt3__navbar-sign {
        display: none;
    }
    .gpt3__navbar-menu_container {
        top: 20px;
    }
    .gpt3__navbar-menu_container-links-sign {
        display: block;
    }
}
