    outline: none;
    background: var(--color-footer);
    color: #fff;
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    border-top-right-radius:5px;
    border-bottom-right-radius:5px;
}
.gpt3__header-content__input button {
    flex: 0.6 1;
    width: 100%;
    min-height: 50px;
    
    background: #FF4820;
    color: #FFF;
    font-family: var(--font-family);
    font-size: 20px;
    font-style: normal;
    font-weight: bold;
    line-height: 27px;
    cursor: pointer;
    outline: none;
    padding: 0 1rem;
    border: none;
    border-top-right-radius:5px;
    border-bottom-right-radius:5px;
}
.gpt3__header-content__people {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2rem;
}
.gpt3__header-content__people img {
    width: 181.79px;
    height: 38px;
}
