.gpt3__blog-container_article {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background: var(--color-footer);
}
.gpt3__blog-container_article-image{
    width: 100%;
    height: 100%;
    background: var(--color-footer);
}
.gpt3__blog-container_article-image img {
    width: 100%;
    height: 100%;
}
.gpt3__blog-container_article-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    height: 100%;
}
.gpt3__blog-container_article-content p {
    color: #FFF;
    font-family: var(--font-family);
    font-size: 11.649px;
    font-weight: 700;
    line-height: 34.947px;
}
.gpt3__blog-container_article-content h3 {
    color: #FFF;
    font-family: var(--font-family);
    font-size: 25.11px;
    font-style: normal;
    font-weight: 800;
    line-height: 30.287px;
    margin-bottom: 5rem;
    cursor: pointer;
}
.gpt3__blog-container_article-content p:last-child {
    color: #FFF;
    font-family: var(--font-family);
    font-size: 11.649px;
    font-weight: 700;
    line-height: 34.947px;
    cursor: pointer;
}
@media screen and (max-width:550px) {
    .gpt3__blog-container_article-content h3 {
        font-size: 18px;
        line-height: 25px;
    }
}