.gpt3__whatgpt3 {
        display: flex;
        flex-direction: column;
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
        padding: 2rem;
        background: var(--color-footer);
       

    /* ff 3.6+ */
    background: -moz-radial-gradient(circle at 120% 150%, rgba(27, 120, 222, 1) 0%, rgba(4, 44, 84, 1) 29%);

    /* safari 5.1+,chrome 10+ */
    background: -webkit-radial-gradient(circle at 120% 150%, rgba(27, 120, 222, 1) 0%, rgba(4, 44, 84, 1) 29%);

    /* opera 11.10+ */
    background: -o-radial-gradient(circle at 120% 150%, rgba(27, 120, 222, 1) 0%, rgba(4, 44, 84, 1) 29%);

    /* ie 10+ */
    background: -ms-radial-gradient(circle at 120% 150%, rgba(27, 120, 222, 1) 0%, rgba(4, 44, 84, 1) 29%);

    /* global 92%+ browsers support */
    background: radial-gradient(circle at 120% 150%, rgba(27, 120, 222, 1) 0%, rgba(4, 44, 84, 1) 29%);

     filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.gpt3__whatgpt3-feauture {
    display: flex;
}
.gpt3__whatgpt3-feauture .gpt3__whatgpt3-feautures-container__feature {
   margin: 0 ;

}
.gpt3__whatgpt3-feauture .gpt3__whatgpt3-feautures-container__feature-text {
    max-width: 700px;
 
 }
.gpt3__whatgpt3-heading {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 4rem 0 2rem;

}
.gpt3__whatgpt3-heading h1 {
    font-family: var(--font-family);
    font-size: 34px;
    font-style: normal;
    font-weight: 800;
    line-height: 45px;
    max-width: 510px;
}
.gpt3__whatgpt3-heading p {
    color: var(--color-subtext);
    font-family: var(--font-family);
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 30px;
    cursor: pointer;
}
.gpt3__whatgpt3-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 2rem;
}
/* Customizing feature component as per needs */
.gpt3__whatgpt3-container .gpt3__features-container__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

/* Customizing feature component as per needs */
.gpt3__whatgpt3-container .gpt3__features-container_feature-text {
    margin-top: 0.5rem;
}

@media screen and (max-width: 850px) {
    .gpt3__whatgpt3-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .gpt3__whatgpt3-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-feature .gpt3__features-container__feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-feature .gpt3__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .gpt3__whatgpt3-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-container .gpt3__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}